.bigContainer {
  width: 270px;
  height: 420px;
  /* background-color: steelblue; */
  background: radial-gradient(circle, #767381, #59575d);
  display: flex;
  flex-direction: column;
  align-content: center;
  border: 5px solid black;
  box-shadow: 8px 6px 3px 1px rgba(0, 0, 0, 1);
  border-radius: 20px 20px 20px 20px;
}

.gamescreen {
  position: relative;
  background-color: lightgrey;
  border: 5px solid black;
  border-bottom: 20px solid black;
  border-top: 10px solid black;
  margin-top: 20px;
  margin-bottom: 30px;
  margin-inline: auto;
}

.buttonLayout {
  position: relative;
  width: 270px;
  height: 75px;
  margin-inline: auto;
  border-top: 0px;
  display: grid;
  grid-template-columns: 3fr 2fr;
  align-items: center;
  justify-content: start;
}

.a-buttonContainer {
  user-select: none;
  width: 40px;
  height: 40px;
  background-color: lightgreen;
  justify-self: end;
  margin-right: 20px;
  display: grid;
  justify-content: center;
  align-content: center;
  border-radius: 20px;
  box-shadow: 4px 4px 3px 1px rgba(0, 0, 0, 0.4);
}

.b-buttonContainer {
  user-select: none;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: red;
  justify-self: end;
  margin-right: 70px;
  margin-top: 20px;
  display: grid;
  justify-content: center;
  align-content: center;
  border-radius: 20px;
  box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.4);
}

.dPad {
  width: 100px;
  height: 75px;
  display: grid;
  margin-left: 10px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}
button {
  background-color: grey;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.4);
  user-select: none;
}

