/* Holds the red circle that the user controls */
.player {
  position: absolute;
  background-color: red;
}

.b-buttonContainer:active {
  width: 32px;
  height: 32px;
  background-color: maroon;
  box-shadow: -1px -1px 1px 1px rgba(0, 0, 0, 0.4);
}
.a-buttonContainer:active {
  width: 42px;
  height: 42px;
  background-color: green;
  box-shadow: -1px -1px 1px 1px rgba(0, 0, 0, 0.4);
}
#score {
  position: absolute;
  display: inline-flex;
  width: 100%;
  height: 10%;
  font-size: 10px;
  margin-top: 3px;
}
#line {
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: black;
  z-index: 99;
  margin-top: 22px;
}
